				<h3 style="margin-bottom:10px">TypeScript warnings on VSCode</h3>
				In some frameworks like Sveltekit on VSCode, TypeScript might not be able to recognize sQuery object.
				If sq.d.ts file doesn't work, put a <b>nocheck comment</b> at the top of the file.<br>
				<img class="cImg" alt="TypeScript issues" src="img/tutorial/typescript/1.png">
				<br>
