<script>
	document.getElementsByTagName('body')[0].style.display = 'none'
	
	import { push } from 'svelte-spa-router'
	import { sQuery as sq } from './sq'
    //import Docs from "./Docs.svelte";
    //import Hello from "./pages/Hello.svelte";
    //import Hello2 from "./pages/Hello2.svelte";

	window.loadProc = function(){
		sq('body').hide().fadeIn(400)

		sq(document).onf('click', '.menu__toggler', function(){
			let el = sq('.menu__toggler')
			el.toggleClass('active')
			if ( sq('.menu__toggler').hasClass('active') )
				sq('#idLeft').fadeIn(500).animate([{left:"-200px"}, {left:"0px"}], 300)
			else	
				sq('#idLeft').fadeOut(500).animate([{left:"0px"}, {left:"-200px"}], 300)
			
			return false
		})
	}

	if (sq().isPageLoaded())
	{
		console.log('spa loaded')
		window.loadProc()
		//setTimeout(() => {
		//	window.loadProc()
		//}, 1);
	}
	sq(()=>{
		console.log('loaded')
		window.loadProc()
	})
</script>

