<script>
	document.getElementsByTagName('body')[0].style.display = 'none'
	
	import {sQuery as sq} from './sq.js';
	//export let name;
	import {name, loadProc} from './docs.js'
	import Sqi from './Sq_editorI.svelte'

	if (sq().isPageLoaded())
	{
		console.log('spa loaded')
		setTimeout(() => {
			loadProc()
			appendReload()
		}, 1);
	}
	sq(()=>{
		loadProc()
		setTimeout(()=>{
			sq('.cJQVer').each(function(){
				const v = sq(this).attr('v')
				sq(this).html(`<a href="https://api.jquery.com/${v}" target=_blank class="notranslate">jQuery ${v}(doc)</a>`)
			})
			appendReload()
		}, 100)
	})
	function appendReload(){
		sq('.cDesc').each(function(){
			sq(this).append('<div class="cReload">reload</div>')
		})
		sq('.cReload').show()
		sq('.cReload').on('click', function(){
			const el = sq(this).parent().parent().find('iframe')
			el.attr('src', el.attr('src') )
		})
	}
</script>

