					<h4>What is SolidJS?</h4>
					<div class="cFWLinkCont"><a class="cFWLink cBlueBack" href="https://reactjs.org/" target=_blank>SolidJS Official site</a></div>
					A declarative, efficient and flexible JavaScript library for building user interfaces.<br>
					Solid stands on the shoulders of giants, particularly React and Knockout. If you've developed with React Hooks before, Solid should seem very natural. In fact, more natural as Solid's model is much simpler with no Hook rules. Every Component executes once and it is the Hooks and bindings that execute many times as their dependencies update.
					Solid follows the same philosophy as React with unidirectional data flow, read/write segregation, and immutable interfaces. It however has a completely different implementation that forgoes using a Virtual DOM.<br><br>
					SolidJS doesn't have the Virtual DOM but it's actually the most performant framework today.
					<br><br>
					SPA/SSR/SSG?: Read <a class="cFWLink cBlueBack" href="https://www.solidjs.com/guides/server" target=_blank>the official documentation</a> for SSR/SSG!
